module.exports = "/* botones de selector de rubro*/\n\n.grupo-botones-rubro {\n    width: 100%;\n    display: flex;\n    flex-wrap: wrap;\n    padding: 15px;\n    border: 0 !important;\n}\n\n.boton-rubro img {\n    height: 38px;\n    width: 38px;\n    margin-top: -25px;\n}\n\n.boton-rubro .mat-button-toggle {\n    flex: 1 1 100%;\n    box-sizing: border-box;\n    align-items: center;\n    display: flex;\n    height: 89px !important;\n    width: 96px !important;\n    border: 1px solid rgba(35, 185, 80, 0.1);\n    border-radius: 4px;\n    background-color: rgba(45, 164, 66, 0.1);\n    margin: 6px;\n}\n\n.boton-rubro .mat-button-toggle-disabled {\n    border: 1px solid #c5c0c7;\n    background-color: #e1dde5;\n}\n\n.boton-rubro .mat-button-toggle-checked {\n    height: 89px;\n    width: 64px;\n    border-radius: 4px;\n    background-color: #2DA442;\n}\n\n.boton-rubro .mat-button-toggle-checked .texto-rubro {\n    color: white !important;\n}\n\n.boton-rubro .mat-button-toggle .texto-rubro {\n    color: #333333;\n}\n\n.texto-rubro {\n    position: absolute;\n    left: -6px;\n    top: 25px;\n    right: -6px;\n    font-family: Rubik;\n    font-size: 14px;\n    text-align: center;\n    text-overflow: ellipsis !important;\n    overflow-x: hidden;\n}\n\n/* titulos */\n\n.titulo-filtro {\n    height: 17px;\n    width: 60px;\n    color: #111111;\n    font-family: Rubik;\n    font-size: 14px;\n    line-height: 17px;\n    padding-left: 20px;\n}\n\n/* encabezado */\n\n.encabezado-filtro {\n    padding-top: 20px;\n}\n\n.texto-filtrar {\n    color: #999999;\n    font-family: Rubik;\n    font-size: 12px;\n    letter-spacing: 1.2px;\n    padding-left: 20px;\n}\n\n.boton-close {\n    margin-top: -35px;\n}\n\n/*fechas*/\n\n.input-fecha {\n    height: 40px;\n    width: 176px;\n    border: 1px solid #2DA442;\n    border-radius: 4px;\n    box-shadow: 0 4px 8px -4px rgba(0, 0, 0, 0.5);\n    padding-left: 10px;\n    color: #333333;\n    font-family: Rubik;\n    font-size: 14px;\n}\n\n.input-label {\n    height: 13px;\n    width: 42px;\n    color: #333333;\n    font-family: Rubik;\n    font-size: 11px;\n    letter-spacing: 1.2px;\n    line-height: 13px;\n}\n\n.contenedores-input {\n    margin-left: 20px;\n}\n\n/*botones*/\n\n.boton-limpiar {\n    height: 40px;\n    width: 104px;\n    border: 1px solid #2DA442;\n    border-radius: 4px;\n    color: #2DA442;\n    font-family: Rubik;\n    font-size: 14px;\n}\n\n.boton-aplicar {\n    height: 40px;\n    width: 104px;\n    background-color: #2DA442;\n    border: 1px solid #2DA442;\n    border-radius: 4px;\n    color: #FFFFFF;\n    font-family: Rubik;\n    font-size: 14px;\n    font-weight: bold;\n}\n\n.contenedor-botones {\n    margin-left: 20px;\n    margin-top: 26px;\n    margin-bottom: 25px;\n}"